export default class ConfirmationYN {
  constructor() {
    this.container = this.createElement('div');
    this.answer = { value: null };
  }

  createElement(name, text = '') { // eslint-disable-line
    const element = document.createElement(`${name}`);
    element.textContent = text;
    return element;
  }

  ask(parentContainer, questionText = 'Are you sure?') {
    this.container.textContent = questionText;
    const buttons = [this.createElement('button', 'Yes'), this.createElement('button', 'No')];
    buttons.forEach((b) => b.addEventListener('click', ({ target }) => {
      this.answer.value = (target.textContent === 'Yes');
    }));
    this.container.append(...buttons);
    parentContainer.append(this.container);
    buttons[0].focus();
  }

  remove() {
    this.container.remove();
  }
}
